html,
body {
  min-height: 100vh
}

body {
  background: white;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 150%;
  color: #121111;
  min-width: 320px;
  position: relative;
  display: flex;
  flex-direction: column;
}

p {
  margin: 0;
}

.center {
  max-width: 1170px;
  margin-right: auto;
  margin-left: auto;
  width: calc(100% - 30px);
  padding-right: 15px;
  padding-left: 15px;
}

.header {
  background: #FFFFFF;
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.08);
  position: fixed;
  width: 100%;
  z-index: 9999999999;

  & .center {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.hamRotate.active {
  transform: rotate(45deg);
}

.line {
  fill:none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke:#000;
  stroke-width:5.5;
  stroke-linecap:round;
}
.ham8 .top {
  stroke-dasharray: 40 160;
}
.ham8 .middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
}
.ham8 .bottom {
  stroke-dasharray: 40 85;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
}
.ham8.active .top {
  stroke-dashoffset: -64px;
}
.ham8.active .middle {
  transform: rotate(90deg);
}
.ham8.active .bottom {
  stroke-dashoffset: -64px;
}

.main {
  flex-grow: 1;
  margin-top: calc(60px + 90px);
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main_img {
  margin-bottom: 20px;
}

.main_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-bottom: 25px;
}

.main_subtitle {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 140%;
  text-align: center;
  color: #000000;
  margin-bottom: 40px;

  & span {
    font-weight: bold;
  }
}

.main_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-bottom: 10px;

  & a {
    text-decoration: none;
    color: #3F8DEE;
  }
}

.footer {
  background: #343532;
  padding-top: 60px;
  padding-bottom: 60px;

  & .center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.footer_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  color: white;
  margin-bottom: 15px;
  text-align: center;
}

.footer_link {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  color: white;
  margin-bottom: 15px;
  text-align: center;

  &:hover {
    color: #3F8DEE
  }
}

.footer_logo {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  color: #FFFFFF;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  text-decoration: none;

  & svg {
    margin-left: 5px;
  }

  &:hover {
    color: #3F8DEE;

    & svg {
      fill: #3F8DEE;
    }
  }
}

.menu {
  background: white;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: scale(0);
  position: fixed;
  margin-top: 0;
  padding-left: 0;
  padding-top: calc(30px + 90px);
  padding-bottom: 30px;
  box-sizing: border-box;

  &.active {
    transform: scale(1);
  }
}

.menu_wrapper {
  transform: scale(0);
  transition: all 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
}

.menu.active .menu_wrapper {
  transform: scale(1);
  transition: all 0.5s ease-in-out ;
}

.menu_link {
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  font-size: 18px;
  text-transform: uppercase;
  color: #000;
  text-decoration: none;
  margin-bottom: 10px;

  &:hover {
    color: #3F8DEE
  }

  &.disabled {
    opacity: 0.3;

    &:hover {
      cursor: auto;
      color: #000;
    }
  }
}

@media(max-width: 990px) {
  .center {
    max-width: 690px;
    margin-right: auto;
    margin-left: auto;
    width: calc(100% - 30px);
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media(max-width: 767px) {
  .center {
    max-width: none;
    margin-right: auto;
    margin-left: auto;
    width: calc(100% - 30px);
    padding-right: 15px;
    padding-left: 15px;
  }

  .header .center {
    padding-top: 0;
    padding-bottom: 0;
  }

  .header_logo {
    width: 110px;
  }

  .main {
    margin-top: calc(40px + 60px);
    margin-bottom: 30px;
  }

  .main_img {
    width: 48px;
    margin-bottom: 15px;
  }

  .main_title {
    font-size: 26px;
    line-height: 120%;
    margin-bottom: 15px;

    & br {
      display: none;
    }
  }

  .main_subtitle {
    font-size: 18px;
    line-height: 130%;
    margin-bottom: 30px;
  }

  .main_text {
    font-size: 14px;
    line-height: 140%;
  }

  .menu {
    padding-top: calc(30px + 60px);
    padding-bottom: 30px;
  }
}
